/* ======== FONTS =============== */

@font-face {
  font-family: light-font;
  src: url("./Assets/Fonts/RedditSans-Light.ttf");
}

@font-face {
  font-family: regular-font;
  src: url("./Assets/Fonts/RedditSans-Regular.ttf");
}

@font-face {
  font-family: italic-font;
  src: url("./Assets/Fonts/RedditSans-MediumItalic.ttf");
}

@font-face {
  font-family: semi-bold-font;
  src: url("./Assets/Fonts/RedditSans-SemiBold.ttf");
}

@font-face {
  font-family: bold-font;
  src: url("./Assets/Fonts/RedditSans-Bold.ttf");
}

/* ================== REUSABLE =============================== */
:root {
  /* colors */
  --color-primary: rgb(0, 0, 0);
  --color-secondary: rgb(237, 237, 237);
  --color-secondary2: rgb(0, 191, 255);
  --color-tertiary: rgb(10, 10, 10);

  /* typography */
  --light-font: light-font;
  --normal-font: regular-font;
  --italic-font: italic-font;
  --semibold-font: semi-bold-font;
  --bold-font: bold-font;
  --small-fontsize: 0.8rem;
  --button-fonsize: 1rem;
  --normal-fontsize: 1.17rem;
  --title-fonsize: 2rem;
  --big-fontsize: 3rem;
  --bigger-fontsize: 4rem;

  /* more */
}

/*  ============== APPLIES TO ALL ELEMENTS ================= */

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  background-color: transparent;
  color: var(--color-secondary);
  font-family: var(--normal-font);
  font-size: var(--normal-fontsize);
}

body {
  background-color: var(--color-primary);
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.body {
  min-width: 100vw;
  min-height: 100vh;
}

.grid {
  display: grid;
  place-content: center;
  place-items: center;
  grid-template-columns: max-content;
}

.icon {
  width: 25px;
  height: 25px;
}

.section_title {
  color: var(--color-secondary2);
  font-size: var(--title-fonsize);
}

.button_primary {
  padding: 0.5rem 1rem;
  background-color: var(--color-secondary);
  color: var(--color-primary);
  border-radius: 0.5rem;
  border: 2px solid var(--color-secondary);
  margin: 10px;
  font-size: var(--button-fonsize);
  transition: all 0.4s ease;
}

.button_primary:hover,
.button_primary:active {
  background-color: var(--color-secondary2);
  color: var(--color-secondary);
  border: 2px solid var(--color-secondary2);
}

.button_secondary {
  padding: 0.5rem 1rem;
  background-color: transparent;
  color: var(--color-secondary2);
  border-radius: 0.5rem;
  border: 2px solid var(--color-secondary2);
  margin: 10px;
  font-size: var(--button-fonsize);
  transition: all 0.4s ease;
}

.button_secondary:hover,
.button_secondary:active {
  background-color: var(--color-secondary2);
  color: var(--color-secondary);
}

.dot {
  display: inline-block;
  width: 8px;
  aspect-ratio: 1;
  margin: 5px;
  border-radius: 50%;
  background-color: var(--color-secondary);
  align-self: center;
  justify-self: center;
}

a {
  color: var(--color-secondary2);
}

a:hover {
  color: var(--color-secondary);
}

/* =================NAVBAR ========================= */
.navbar {
  width: 100vw;
  height: 60px;
  position: relative;
  background-color: var(--color-tertiary);
  box-shadow: 2px 3px 5px var(--color-secondary);
  margin-bottom: 10px;
  transition: all 0.4s ease;
}

.navbar:hover {
  box-shadow: 2px 3px 5px var(--color-secondary2);
}

.nav_container {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.logo-container {
  width: fit-content;
  height: fit-content;
}

.logo {
  width: 60px;
}

.links {
  display: flex;
  align-items: center;
  gap: 3rem;
}

.nav_toggle {
  display: none;
}

/* Mobile Navbar Navigation */
@media screen and (max-width: 575px) {
  :root {
    --normal-fontsize: 1rem;
    --title-fonsize: 1.5rem;
  }

  .navbar {
    background-color: transparent;
  }
  .nav_container {
    position: fixed;
    height: 100vh;
    width: 100vw;
    background-color: rgba(10, 10, 10, 0.5);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    flex-direction: column;
    justify-content: start;
    align-items: flex-start;
    z-index: 2;
    transition: all 0.4s;
  }

  .close-menu {
    left: -100vw;
  }

  .logo-container {
    align-self: flex-start;
  }

  .logo {
    width: 120px;
  }

  .links {
    flex-direction: column;
    align-self: center;
    justify-self: center;
    height: 80%;
    width: 100%;
    align-items: center;
    justify-content: center;
  }

  .nav_toggle {
    position: fixed;
    z-index: 1000;
    display: block;
    top: 1rem;
    right: 1.5rem;
  }
}

/* ================ home ==================== */
.home_container {
  padding: 10px;
  gap: 20px;
}

.home_container h2,
.home_container h2 span {
  font-size: var(--big-fontsize);
}

.home_container h2 span {
  color: var(--color-secondary2);
}

.social_links {
  display: flex;
  align-items: center;
  width: 80%;
  height: 120px;
  background-color: var(--color-tertiary);
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  gap: 5px;
  border-radius: 3rem;
  margin-top: 20px;
}

.social_links h4 {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.social_links h4 img {
  width: 1.2rem;
}

.arrow,
.connect {
  transition: all 0.4s ease-in-out;
}

.connect:hover .arrow {
  transform: translateX(12px);
}

.social_links h4 span:hover {
  color: var(--color-secondary2);
}

.socials {
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 15px;
}

.socials a img {
  width: 35px;
  height: 35px;
  transition: all 0.4s ease;
}

.socials a img:hover {
  transform: translateY(-6px);
}

.home_buttons {
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 300px;
}

/* FOOTER */
footer {
  height: fit-content;
  padding: 20px;
  background-color: var(--color-tertiary);
  text-align: center;
  font-size: calc(var(--normal-fontsize) - 6px);
}

.footer {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.footer_logo_img {
  width: 80px;
}

.footer_links {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  font-size: calc(var(--normal-fontsize) - 6px);
}

.footer_links a {
  font-size: calc(var(--normal-fontsize) - 6px);
}

footer p {
  font-size: calc(var(--normal-fontsize) - 6px);
}

/* ============================= PROJECTS ===================== */
.projects {
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: flex-start;
  gap: 10px;
}

.maque {
  background-color: var(--color-tertiary);
  width: 90%;
  height: 140px;
  max-width: 1536px;
  margin-inline: auto;
  position: relative;
  margin-top: 0;
  border-radius: 1rem;
  overflow: hidden;
  mask-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 1) 20%,
    rgba(0, 0, 0, 1) 80%,
    rgba(0, 0, 0, 0)
  );
}

@keyframes scroll-left {
  to {
    left: -200px;
  }
}

.maque_item {
  width: 200px;
  height: 100%;
  background-color: var(--color-secondary);
  border-radius: 1rem;
  position: absolute;
  left: max(calc(200px * 14), 100%);
  overflow: hidden;
  animation: scroll-left 30s linear infinite;
}

.item1 {
  animation-delay: calc(30s / 14 * (14 - 1) * -1);
}

.item2 {
  animation-delay: calc(30s / 14 * (14 - 2) * -1);
}

.item3 {
  animation-delay: calc(30s / 14 * (14 - 3) * -1);
}

.item4 {
  animation-delay: calc(30s / 14 * (14 - 4) * -1);
}

.item5 {
  animation-delay: calc(30s / 14 * (14 - 5) * -1);
}

.item6 {
  animation-delay: calc(30s / 14 * (14 - 6) * -1);
}

.item7 {
  animation-delay: calc(30s / 14 * (14 - 7) * -1);
}

.item8 {
  animation-delay: calc(30s / 14 * (14 - 8) * -1);
}

.item9 {
  animation-delay: calc(30s / 14 * (14 - 9) * -1);
}

.item10 {
  animation-delay: calc(30s / 14 * (14 - 10) * -1);
}

.item11 {
  animation-delay: calc(30s / 14 * (14 - 11) * -1);
}

.item12 {
  animation-delay: calc(30s / 14 * (14 - 12) * -1);
}

.item13 {
  animation-delay: calc(30s / 14 * (14 - 13) * -1);
}

.item14 {
  animation-delay: calc(30s / 14 * (14 - 14) * -1);
}

.maque_item img {
  width: 100%;
  height: 100%;
}

.projects_cards {
  margin: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;
  width: 100vw;
}

.card {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 10px;
  height: 420px;
  width: 90%;
  background-color: var(--color-tertiary);
}

.card_image {
  background-color: var(--color-secondary2);
  gap: 10px;
  overflow: hidden;
}

.card_image img {
  width: 100%;
  height: 100%;
  transition: all 0.4s ease;
}

.card_image:hover .project_img {
  transform: scale(1.2);
}

.tags {
  display: flex;
  gap: 10px;
}

.tag {
  padding: 10px;
  background-color: var(--color-secondary);
  color: var(--color-primary);
  cursor: pointer;
}

.card-details {
  display: flex;
  align-items: start;
  justify-content: start;
  gap: 20px;
  flex-direction: column;
}

.title {
  font-family: var(--bold-font);
  font-size: var(--title-fonsize);
}

.projects_footer {
  text-align: center;
}

@media screen and (max-width: 875px) {
  .card {
    grid-template-columns: max-content;
    height: fit-content;
  }

  .card_image {
    width: 90vw;
    aspect-ratio: 16/9;
  }

  .title,
  .card-details {
    max-width: 90vw;
  }
}

/* ==================== SKILLS ===================== */
.skills {
  flex-direction: column;
  justify-content: start;
  gap: 40px;
}

.skills_container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: center;
  justify-content: center;
}

.skill_card {
  width: 250px;
  height: 120px;
  background-color: var(--color-tertiary);
  display: flex;
  align-items: center;
  justify-content: space-around;
  transition: all 0.4s ease;
  overflow: hidden;
}

.skill_img {
  width: 70px;
  transition: all 0.4s ease;
}

.skill_title {
  transition: all 0.4s ease;
}

.skill_card:hover {
  background-color: var(--color-secondary);
}

.skill_card:hover .skill_title {
  color: var(--color-tertiary);
}

.skill_card:hover .skill_img {
  transform: scale(1.2);
}

/* ================== ABOUT ======================= */
.about {
  flex-direction: column;
  padding: 20px;
}

.about_content {
  width: 80vw;
}

.section {
  margin-top: 30px;
}

.section h3 {
  background-color: var(--color-secondary);
  color: var(--color-tertiary);
  text-align: center;
  padding: 10px;
}

.about_list {
  list-style: circle;
}

.about_list li {
  list-style: square;
  font-size: calc(var(--normal-fontsize) - 6px);
}

/* ===================== CONTACT =====================*/
.contact,
.content {
  flex-direction: column;
}

.contact {
  justify-content: start;
  gap: 30px;
}

input,
textarea {
  resize: none;
  outline: none;
  padding: 10px;
}

.contact_container {
  gap: 2rem;
  flex-wrap: wrap;
}

.contact_container .form form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;
  padding: 20px;
}

.form form input,
.form form textarea {
  width: 320px;
  background-color: var(--color-tertiary);
  border: none;
  border-radius: 0.5rem;
  height: 70px;
  transition: all 0.4s ease-in-out;
}

.form form input:hover,
.form form textarea:hover {
  background-color: rgb(25, 10, 10);
}

.form form input:focus,
.form form textarea:focus {
  border: 2px solid var(--color-secondary);
}

.form form textarea {
  height: 200px;
}

.other_contacts {
  margin: 20px;
}

.contact_means {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 3rem;
}

.contact_means a {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  padding: 10px 30px;
  width: 250px;
  border-radius: 1rem;
  background-color: var(--color-tertiary);
  border: 2px solid transparent;
  transition: all 0.4s ease;
}

.contact_means a:hover {
  border: 2px solid var(--color-secondary);
}

.contact_means a img {
  width: 35px;
}

/* ===================== PAGE NOT FOUND ===================== */
.not_found {
  text-align: center;
}
.not_found h1 {
  font-family: var(--bold-font);
  font-size: var(--bigger-fontsize);
}

.not_found h2 {
  font-family: var(--semibold-font);
  font-size: var(--big-fontsize);
}

/*=============== SCROLL BAR ===============*/
::-webkit-scrollbar {
  width: 0.6rem;
  border-radius: 0.5rem;
  background-color: hsla(0, 0%, 10%, 0.1);
}

::-webkit-scrollbar-thumb {
  border-radius: 0.5rem;
  background-color: hsl(0, 0%, 20%);
}

::-webkit-scrollbar-thumb:hover {
  background-color: hsl(0, 0%, 30%);
}
